<template>
  <div class="text-center">
    <h4 class="mdl-color-text--primary">Purchase Raw Material</h4>
      <order-view
        :clientData="vendors.data"
        :clientAction="fetchVendors"
        :productData="rawMaterials.data"
        :productAction="fetchRawMaterials"
        :productFk="'raw_material'"
        :clientFk="'vendor'"
        :order="orderParams"
        @orderComplete="orderParams=$event"
      ></order-view>
  </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex'
import { OrderView } from 'SHARED/orders'
export default {
  name: 'raw-material-purchase-form',
  components: {
    OrderView
  },
  props: {
    order: {
      type: Object
    },
    edit: {
      type: Boolean,
      default: false
    }
  },
  mounted () {
    window.componentHandler.upgradeAllRegistered()
  },
  computed: {
    ...mapGetters({
      rawMaterials: 'getRawmaterials',
      vendors: 'getVendors'
    }),
    orderParams: {
      get () {
        return this.order || this.params
      },
      set (data) {
        this.$set(this, 'params', data)
        this.createOrder()
      }
    }
  },
  methods: {
    ...mapActions([
      'fetchRawMaterials',
      'fetchVendors',
      'createRawMaterialPurchaseOrder'
    ]),
    createOrder () {
      this.createRawMaterialPurchaseOrder(this.orderParams).then(() => {
        this.$toastr.s('Successfully created')
        this.$emit('completed')
      }).catch(e => {
        this.$toastr.e('Please verify error')
      })
    }
  },
  data () {
    return {
      params: {
        vendor: undefined,
        name: '',
        address: '',
        line_items: [],
        transactions: [],
        sgst_percentage: 0,
        cgst_percentage: 0,
        igst_percentage: 0
      }
    }
  }
}
</script>




